body{display:flex;flex-flow:column nowrap;max-height:100vh}main{display:flex;flex-flow:row nowrap;overflow-y:hidden;overflow-x:scroll}main>.list-container>.list{display:flex;flex-flow:column nowrap;width:300px;max-height:100%}main>div>div>ul{display:flex;flex-flow:column;overflow-y:scroll}body{background-color:#333}input,button{outline:none}ul{list-style-type:none;padding:0;box-sizing:border-box}ul li{background-color:#FAFAFA;margin:0.25em 0;box-sizing:border-box}ul input{box-sizing:border-box;width:100%;border:none;background-color:transparent;padding:0.25em;color:#000}main{padding-bottom:1em}.list-container{padding:0 1em}.list{background-color:#eee;padding:0.5em;border-radius:4px}.list input{user-select:none}.new-item-input{width:100%;border:none;background-color:transparent}.list-title{width:100%;flex:0 0 1.5em;display:flex}.list-title input{width:100%;border:none;background-color:transparent}.list-title button{background-color:transparent;border:none}.list-container>.add-list{background-color:rgba(250,250,250,0.2)}.list-container.add-list{background-color:transparent}.list-container.add-list input{background-color:transparent;border:none;color:#FAFAFA}.item-complete{opacity:.4;transition:opacity .2s ease-in-out}.item-complete:hover{opacity:1}header{color:#FAFAFA;display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;height:2.5em;font-size:1.75em;font-family:'Yellowtail', cursive}div.list ul::-webkit-scrollbar{-webkit-appearance:none;width:7px;left:2px}div.list ul::-webkit-scrollbar-thumb{border-radius:4px;background-color:rgba(0,0,0,0.5);box-shadow:0 0 1px rgba(255,255,255,0.5)}.preview-image{width:100%;max-width:100%;height:auto}
